import React from 'react';
import ProjectSplash from './projects-splash/project-splash.js';
import ProjectGrid from './projects-grid/projects-grid.js';
import Footer from './footer/footer.js';
import Hummingbird from './projects/hummingbird.js';
import CssGarden from './projects/cssgarden.js';
import BritCitizen from './projects/britquiz.js';
import StampTax from './projects/stamptax.js';
import Calculator from './projects/calculator.js';
import WikiSearch from './projects/wikisearch.js';
import YouTubeClone from './projects/youtube.js';

class Projects extends React.Component {
  constructor(props){
    super(props);
    this.state = {
        images: [
        {
          id: 1,
          image: 'images/p-grid-imgs/hummingbirdben.jpg',
          title: 'Hummingbird Visuals',
          alt:'Hummingbird Visuals, picture of Benjamin May',
          link: '/hummingbird-visuals',
          component: Hummingbird
        },
        {
          id: 2,
          image: 'images/p-grid-imgs/yt.png',
          title: 'YouTube Clone',
          alt:'YouTube Logo',
          link: '/youtube-clone',
          component: YouTubeClone
        },
        {
          id: 3,
          image: 'images/p-grid-imgs/calculator.jpg',
          title:'JavaScript Calculator',
          alt:'Green Calculator',
          link: '/calculator',
          component: Calculator
        },
        {
          id: 4,
          image: 'images/p-grid-imgs/quiz.jpg',
          title:'British Citizen Quiz',
          alt:'Quiz vector image',
          link:'/british-citizen-quiz',
          component: BritCitizen
        },
        {
          id: 5,
          image: 'images/p-grid-imgs/houses.jpg',
          title:'Stampduty Calculator',
          alt:'Earls Court, London Houses',
          link: '/stampduty-calculator',
          component: StampTax
        },
        {
          id: 6,
          image: 'images/p-grid-imgs/css-zen-garden.jpg',
          title:'Css Zen Garden',
          tech:['HTML', 'css'],
          alt:'Css Zen Garden image',
          link: '/css-zen-garden',
          component: CssGarden
        },
        {
          id: 7,
          image: 'images/p-grid-imgs/encyclopedia.jpg',
          title:'Wikipedia Search',
          alt:'Books',
          link:'/wikipedia-search',
          component: WikiSearch
        }
      ]
    };
  };

  componentDidMount() {
    window.scrollTo(0,0);
  }

  render() {
    return (
      <div className="projects">
      <ProjectSplash />
      <ProjectGrid gridPictures={this.state.images}/>
      <Footer />
    </div>
    )
  }
}

export default Projects;



