import React from 'react';
import './arrow.min.css';

const Arrow = (props) =>{

  function scrollIt(arg) {
    document.querySelector(props.arg).scrollIntoView({behavior: 'smooth'})
  };
  return (
    <i className="fa fa-angle-down" onClick={scrollIt} aria-hidden="true"></i>
  )
}

export default Arrow;



