import React from 'react';
import {Link} from 'react-router-dom';
import ButtonEx from '../button-external/button.js';
import './styles/projects-page.min.css';

class YouTubeClone extends React.Component {

  componentDidMount() {
    if (!document.querySelector('.project-grid-list')) {
      window.scrollTo(0,0);
    } else {
    var images = document.querySelector('.project-grid-list');
    var footer = document.querySelector('.footer');
    images.style.height = 0;
    images.style.marginLeft = '200%';
    footer.style.display = 'none';
    window.scrollTo(0,0);
  }

  }

  componentWillUnmount(){
    if (!document.querySelector('.project-grid-list')) {
      return null;
    } else {
    var images = document.querySelector('.project-grid-list');
    var footer = document.querySelector('.footer');
    images.style.height = '100%';
    images.style.marginLeft = 0;
    footer.style.display = 'flex';
    window.scrollTo(0, 400);
    }
  }

  render() {
    let buttonProps = {
      link: 'https://jamesbarford.github.io/youtube-clone/',
      text: 'View Project'
    }
    let background = {
      background: `url(${'images/p-grid-imgs/yt.png'})`,
      backgroundSize: 'contain',
      backgroundPosition: '50% 50%',
      backgroundRepeat: 'no-repeat',
      backgroundColor: '#333'
    };
    return (
      <div className="project-page-wrap">
        <Link className="back-to-projects" to='/projects'><i className="fa fa-angle-double-left" aria-hidden="true"></i> projects</Link>
          <div className="project-page-block-2" style={background}/>
          <div className="project-page-blurb">
            <div className="project-page-block-1">
              <p>A single page React application for searching YouTube and watching videos. Calling YouTube's
                API to display videos. Made as part of the Udemy onilne course 'Modern React with Redux'.
              </p>
              <div className="side-header">
                <h1>YouTube Clone</h1>
                <h2 className="project-sub-header">React Application</h2>
                <h3>Udemy</h3>
              </div>
            </div>
          </div>
          <ButtonEx
            link={buttonProps.link}
            text={buttonProps.text}/>
        </div>
    )
  }
};

export default YouTubeClone;



