import React from 'react'
import TechList from './tech-list/tech-list.js';

const TechStack = (props) => {
  const listItems = props.makeList.map(content => {
    return (
      <TechList
        key={content.id}
        listContent={content.image}
        alt={content.alt}
       />
    );
  });
  return (
    <div className="main-list">
      <h2>Tech Stack</h2>
        <ul className="list-group">
          {listItems}
        </ul>
      </div>
  );
};

export default TechStack;



