import React from 'react';
import ProjectsGridItem from './projects-grid-item.js';

const ProjectGrid = (props) => {
  const listItems = props.gridPictures.map(images => {
    return (
      <ProjectsGridItem
        key={images.id}
        listContent={images.image}
        alt={images.alt}
        title={images.title}
        link={images.link}
        component={images.component}
       />
    );
  });

  return (
    <div className="project-grid">
      <h2>Projects</h2>
        <ul className="project-grid-list" >
          {listItems}
        </ul>
      </div>
  );
};

export default ProjectGrid;

