import React from 'react';
import { Switch, Route } from 'react-router-dom';
import YouTubeClone from './projects/youtube.js';
import Hummingbird from './projects/hummingbird.js';
import StampTax from './projects/stamptax.js';
import Projects from './projects.js';
import About from './about.js';
import Home from './home.js';

// The Main component renders one of the three provided
// Routes (provided that one matches)
const Main = () => (
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route path='/projects' component={Projects}/>
      <Route path='/hummingbird-visuals' component={Hummingbird}/>
      <Route path='/stampduty-calculator' component={StampTax}/>
      <Route path='/youtube-clone' component={YouTubeClone}/>
    </Switch>
)

export default Main;



