import React from 'react';
import Button from '../button-comp/button.js';
import {Link} from 'react-router-dom';
import './selected-projects.min.css';


const SelectedProjects = () => {

let buttonProps = {
  link: '/projects',
  text: 'View More'
}

  return (
    <div className="selected-projects">
      <span className="selected-projects-title"><h2>Selected Projects</h2></span>
      <div className="selected-projects-gallery">
        <span className="selected-projects-item">
          <Link to='/hummingbird-visuals'>
            <img className="project-image" src="images/p-grid-imgs/hummingbirdben.jpg" alt="Hummingbird Visuals, Benjamin May"/>
            <h3>Hummingbird Visuals</h3>
            <p>Photography Portfolio</p>
          </Link>
        </span>
        <span className="selected-projects-item">
          <Link to='/stampduty-calculator'>
            <img className="project-image" src="images/p-grid-imgs/houses.jpg" alt="Earls Court"/>
            <h3>Stampduty Calculator</h3>
            <p>JavaScript Stampduty Calculator</p>
          </Link>
        </span>
        <span className="selected-projects-item">
          <Link to='/youtube-clone'>
          <img className="project-image" src="images/p-grid-imgs/yt.png" alt="YouTube Logo"/>
          <h3>YouTube Clone</h3>
          <p>React YouTube Application</p>
        </Link>
        </span>
      </div>
      <span className="selected-projects-btn">
        <Button
          link={buttonProps.link}
          text={buttonProps.text} />
      </span>
    </div>
  );
};

export default SelectedProjects;



