import React from 'react';
import {Link} from 'react-router-dom';
import './button-style.min.css';

const Button = (props) => {
  return (
    <ul className="button">
      <li className="button-item">
        <div className="button-link">
          <Link to={props.link}>
            <span className="button-link-background"></span>
            <span className="button-link-label">{props.text}</span>
          </Link>
        </div>
      </li>
    </ul>
  );
};

export default Button;



