import React from 'react';
import {Link} from 'react-router-dom';
import './navigation.min.css';

class Navigation extends React.Component {
  constructor(props){
  super(props)
  this.state = {
    navColor: false
  };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.changeColor);
  }

  componentWillUnmount(){
    window.removeEventListener('scroll');
  }
  changeColor() {
    const startChange = document.querySelector('.main-list') || document.querySelector('.project-grid');
    if(!startChange) {
      return null;
    }
    const white = '#f8f8ff';
    const offset = startChange.offsetTop;
    const navText = document.querySelector('.nav-ul');

    if (window.scrollY + 30 < offset) {
      navText.style.color = white;
    } else {
      navText.style.color = '#333';
    }
  }

  render() {
    return (
      <div className="nav">
        <ul className="nav-ul">
          <li className="nav-li"><Link to='/'>James Barford-Evans</Link></li>
          <li className="nav-li"><Link to='/projects'>Projects</Link></li>
        </ul>
      </div>
    )
  };
};

export default Navigation;



