import React from 'react';
import {Link} from 'react-router-dom';
import ButtonEx from '../button-external/button.js';
import './styles/projects-page.min.css';

class Calculator extends React.Component {

  componentDidMount() {
    if (!document.querySelector('.project-grid-list')) {
      window.scrollTo(0,0);
    } else {
    var images = document.querySelector('.project-grid-list');
    var footer = document.querySelector('.footer');
    images.style.height = 0;
    images.style.marginLeft = '200%';
    footer.style.display = 'none';
    window.scrollTo(0,0);
  }
}

  componentWillUnmount(){
    if (!document.querySelector('.project-grid-list')) {
      return null;
    } else {
    var images = document.querySelector('.project-grid-list');
    var footer = document.querySelector('.footer');
    images.style.height = '100%';
    images.style.marginLeft = 0;
    footer.style.display = 'flex';
    window.scrollTo(0, 400);
    }
  }

  render() {
    let buttonProps = {
      link: 'https://jamesbarford.github.io/javascript-calculator/',
      text: 'View Project'
    }
    let background = {
      background: `url(${'images/p-grid-imgs/calculator.jpg'})`,
      backgroundSize: 'cover',
      backgroundPosition: '0% 50%',
      backgroundRepeat: 'no-repeat',
      backgroundColor: '#2E2E2E'
    };
    return (
      <div className="project-page-wrap">
        <Link className="back-to-projects" to='/projects'><i className="fa fa-angle-double-left" aria-hidden="true"></i> projects</Link>
          <div className="project-page-block-2" style={background}/>
          <div className="project-page-blurb">
            <div className="project-page-block-1">
              <p>A simple JavaScript calculator for addition, subtraction, division &amp; mulitplication.
              Made as part of 'Programming for the Web with JavaScript' course by The University of Pennsylvania.
              </p>
              <div className="side-header">
                <h1>Calculator</h1>
                <h2 className="project-sub-header">A JavaScript Application</h2>
                <h3>University of Pennsylvania</h3>
              </div>
            </div>
          </div>
          <ButtonEx
            link={buttonProps.link}
            text={buttonProps.text}/>
        </div>
    )
  }
};

export default Calculator;



