import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';
import './projects-grid-item/projects-grid.min.css';

const ProjectsGridItem = (props) => {
  return (
    <Router>
      <li className="project-grid-items">
        <Link to={props.link} className="projects-link">
        <img className="project-grid-img" src={props.listContent} alt={props.alt}/>
        <h3>{props.title}</h3></Link>
          <Route path={props.link} component={props.component}/>
      </li>
    </Router>
    )
  }
export default ProjectsGridItem;



