import React from 'react';
import TechStack from './tech-stack/tech-stack.js';
import SelectedProjects from './selected-projects/selected-projects.js';
import Footer from './footer/footer.js';
import './styles/home.min.css';
import Arrow from './arrow-down/arrow-down.js';

class Home extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      listContent: [
          {id: 1, image: 'logos/css-logo.png', alt: 'css logo'},
          {id: 2, image: 'logos/html-logo.png', alt: 'html logo'},
          {id: 3, image: 'logos/javascript-logo.png', alt:'javascript logo'},
          {id: 4, image: 'logos/jquery-logo.png', alt:'jquery logo'},
          {id: 5, image: 'logos/react-logo.png', alt:'react logo'},
          {id: 6, image: 'logos/sass-logo.png', alt:'sass logo'}
      ]
    };
  }

  componentDidMount() {
    window.scrollTo(0,0);
  }

    render() {
    return (
      <section className="home">
        <div className="home-text-wrap">
          <div className="home-text">
            <h1>James Barford-Evans</h1> <br></br><h2>Frontend Developer</h2>
            <p>Hello! I am an energetic, enthusiastic, creative chap with experience in Digital Marketing, Web Design and Front End Development.</p>
          </div>
          <Arrow arg={".main-list"}/>
        </div>
        <TechStack
            makeList={this.state.listContent}
         />
         <SelectedProjects />
         <Footer/>
      </section>
    );
  }
};

export default Home;



